.popup-preview {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  padding-top: 143px;
  overflow: auto;

  background-color: rgba(109, 109, 109, 0.9);
  z-index: 50;
}

// .popup-preview__overlay {
//   position: absolute;
//   top: 0;
//   left: 0;

//   width: 100%;
//   min-height: 100%;

//   background-color: rgba(109, 109, 109, 0.9);
// }

.popup-preview__info {
  position: absolute;

  left: $desktop-oversize-gap;
  top: 190px;

  width: 925px;
  min-height: 700px;
  padding: 76px 136px 70px;

  background-color: #ffffff;

  z-index: 9;
}

.popup-preview__type {
  margin: 0;

  font-weight: 300;
  font-size: 18px;
  line-height: 140%;
}

.popup-preview__name {
  margin: 0;
  margin-bottom: 24px;

  font-weight: 700;
  font-size: 60px;
  line-height: 120%;
  color: #000000;
  text-transform: uppercase;
}

.popup-preview__description {
  margin: 0;
  margin-bottom: 23px;

  line-height: 140%;
}

.popup-preview__country,
.popup-preview__material {
  display: inline-block;
  margin: 0;
}

.popup-preview__country {
  margin-right: 33px;
  margin-bottom: 58px;
}

.popup-preview fieldset {
  padding: 0;
  margin-left: -1px;

  border: none;
}

.popup-preview legend {
  margin-bottom: 17px;

  font-weight: 500;
}

.popup-preview ul {
  @include list-reset;

  display: flex;
  margin-bottom: 40px;
}

.popup-preview li {
  &:not(:last-child) {
    margin-right: 20px;
  }
}

.popup-preview label {
  display: block;
  width: 115px;
  min-height: 60px;
  padding-top: 15px;

  color: #6d6d6d;
  text-align: center;

  border: 2px solid #6d6d6d;

  &:hover {
    background-color: #6d6d6d;
    color: #ffffff;
    cursor: pointer;
  }

  &:active {
    background-color: #606060;
  }
}

.popup-preview input:focus + label {
  background-color: #6d6d6d;
  color: #ffffff;
  cursor: pointer;
}

.popup-preview input:checked + label {
  background-color: #606060;
  color: #ffffff;
}

.popup-preview__to-cart {
  width: 250px;
  margin-left: 270px;
  padding-top: 24px;
  padding-bottom: 20px;

  font-weight: 500;
  font-size: 16px;
  line-height: 16px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

.popup-preview__price {
  margin-top: -47px;
  font-weight: 500;
  font-size: 28px;
  line-height: 33px;
  color: #000000;
}

.popup-preview__slider {
  position: absolute;
  top: 143px;
  right: 0;

  height: 795px;
  margin-bottom: 50px;
  padding-right: $desktop-oversize-gap;
}

.popup-fullview__slider {
  position: absolute;
  top: 0;
  width: 100%;
  height: auto;
  max-height: 1080px;

  z-index: 25;
}

.popup-fullview__slide {
  max-width: 1920px;
  left: calc((100vw - 1920px) / 2);
}

.popup-fullview__slider img {
  width: 100%;
}

.popup-preview__slide {
  background-color: #c4c4c4;

  &:hover {
    cursor: url("../img/zoom-in.png"), crosshair;
  }
}

.popup-preview__slider > .popup-preview__pagination,
.popup-fullview__slider > .popup-fullview__pagination {
  right: $desktop-oversize-gap;

  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: -110px;

  z-index: 10000000000;
}

.popup-preview__slider > .popup-preview__pagination .swiper-pagination-bullet,
.popup-fullview__slider > .popup-fullview__pagination .swiper-pagination-bullet {
  width: 6px;
  height: 6px;
  margin: 12px 0;

  background: none;
  background-color: #ffffff;
  opacity: 1;

  &-active {
    width: 12px;
    height: 12px;
    margin: 9px 0;
  }
}

.popup-fullview__slide:hover {
  cursor: url("../img/zoom-out.png"), crosshair;
}

.popup-fullview__slider img {
  width: 1920px;
  height: 1080px;
}

.popup-preview__close {
  @include hover;
  @include button-reset;

  position: absolute;
  top: 55px;
  right: 55px;

  font-weight: 300;
  font-size: 18px;
  line-height: 140%;
  color: #ffffff;
}

.popup-fullview__close {
  display: none;
}

@include desktop-l {
  .popup-preview__info {
    left: $desktop-gap;
  }

  .popup-preview__slider {
    padding-right: $desktop-gap;
  }

  .popup-preview__slider > .popup-preview__pagination,
  .popup-fullview__slider > .popup-fullview__pagination {
    right: 50px;

    margin-right: 0;
  }

  .popup-fullview__slide {
    width: 1920px;
    right: 0;
  }

  .popup-fullview__slider .popup-preview__pagination {
    right: 55px;
  }
}

@include desktop {
  .popup-preview__info {
    left: 3%;
    top: 60px;
    width: 60%;
    padding-left: 60px;
    padding-right: 100px;
    min-height: 750px;
  }

  .popup-preview__slider {
    top: 90px;
    right: 3%;
    width: 44%;
    padding-right: 0;
    height: 700px;
  }

  .popup-preview__slide img {
    height: 100%;
    width: auto;
    max-width: none;
  }
}

@include tablet {
  .popup-preview__info {
    width: 100%;
    left: 0;
    top: 500px;
  }

  .popup-preview__slider {
    top: 0;
    right: 0;
    width: 100%;
    height: 500px;
  }

  .popup-preview__slide {
    min-height: 500px;
  }

  .popup-preview__slide img {
    height: auto;
    width: 1195px;
  }

  .popup-fullview__slider {
    height: 100vh;
  }

  .popup-preview__close span {
    display: none;
  }

  .popup-preview__close {
    @include hover;

    position: absolute;
    top: 32px;
    right: 32px;

    width: 50px;
    height: 50px;

    background-color: #ffffff;
    background-image: url("../img/icon-close.svg");
    background-repeat: no-repeat;
    background-position: center;
    z-index: 20;
  }

  .popup-fullview__close {
    @include button-reset;
    @include hover;

    position: absolute;
    top: 16px;
    right: 16px;

    display: block;
    width: 50px;
    height: 50px;

    background-color: #ffffff;
    background-image: url("../img/icon-close.svg");
    background-repeat: no-repeat;
    background-position: center;
    z-index: 26;
  }
}

@include mobile {
  .popup-preview {
    background-color: #000000;
  }

  .popup-preview__info {
    top: 213px;
    width: 100%;
    padding: 37px 16px 12px;
    min-height: 771px;

    background: linear-gradient(to top, #c4c4c4, #c4c4c4 55px, #ffffff 55px, #ffffff);
  }

  .popup-preview__type {
    margin-bottom: 8px;

    font-size: 14px;
    line-height: 140%;
  }

  .popup-preview__name {
    margin-bottom: 7px;

    font-size: 28px;
    line-height: 120%;
  }

  .popup-preview__description,
  .popup-preview__country,
  .popup-preview__material {
    font-size: 14px;
    line-height: 20px;
  }

  .popup-preview__description {
    margin-bottom: 10px;
    padding-left: 1px;
  }

  .popup-preview__country {
    padding-left: 2px;
    margin-bottom: 11px;
  }

  .popup-preview__material {
    padding-left: 2px;
    margin-bottom: 33px;
  }

  .popup-preview fieldset {
    max-width: 288px;
    margin: 0 auto;
  }

  .popup-preview legend {
    padding-left: 2px;
    margin-bottom: 19px;

    font-size: 14px;
    line-height: 16px;
  }

  .popup-preview ul {
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 72px;
  }

  .popup-preview li {
    margin-bottom: 16px;

    &:not(:last-child) {
      margin-right: 0;
    }
  }

  .popup-preview label {
    width: 136px;
    font-size: 14px;
  }

  .popup-preview__to-cart {
    display: block;
    margin-left: 0;
    margin: 0 auto;
    width: 288px;
  }

  .popup-preview__price {
    display: block;
    max-width: 288px;
    margin-top: -119px;
    margin-left: auto;
    margin-right: auto;
  }

  .popup-preview__slider {
    height: 243px;
    width: 320px;
    right: 50%;
    margin-right: -160px;
    padding-bottom: 30px;

    z-index: 10;
  }

  .popup-preview__slide {
    min-height: auto;
  }

  .popup-preview__slide img {
    height: 213px;
    width: auto;
  }

  .popup-fullview__slider {
    max-height: 640px;
  }

  .popup-fullview__slide {
    left: 0;
    width: 100%;
  }

  .popup-fullview__slider img {
    width: auto;
    max-width: none;
    height: 640px;
  }

  .popup-preview__slider > .popup-preview__pagination,
  .popup-fullview__slider > .popup-fullview__pagination {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-bottom: -3px;
  }

  .popup-preview__slider > .popup-preview__pagination .swiper-pagination-bullet,
  .popup-fullview__slider > .popup-fullview__pagination .swiper-pagination-bullet {
    margin: 0 12px;
    background-color: #6d6d6d;

    &-active {
      margin: 0 9px;
    }
  }

  .popup-preview__close {
    top: 16px;
    right: calc((100vw - 320px) / 2 + 16px);
  }

  .popup-fullview__slider .swiper-pagination {
    display: none;
  }
}





