.checkout {
  position: relative;
  max-width: 925px;
  width: 55%;
  margin-bottom: 200px;
  //margin-left: calc((100vw - 1600px)/2 + 135px);
  margin-left: 15.4%;
  margin-top: -106px;

  @include desktop {
    width: auto;
    max-width: 790px;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 20px;
  }

  @include tablet {
    width: 77%;
  }

  @include mobile {
    width: 100%;
    margin-bottom: 16px;
  }
}

.checkout__group {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
  padding-top: 98px;
  padding-right: 125px;
  padding-bottom: 98px;
  padding-left: 125px;
  line-height: 22px;
  background-color: $white;

  @include tablet {
    padding-top: 50px;
    padding-right: 50px;
    padding-bottom: 50px;
    padding-left: 50px;
  }

  @include mobile {
    margin-bottom: 16px;
    padding-top: 15px;
    padding-right: 5px;
    padding-bottom: 15px;
    padding-left: 5px;
    font-size: 14px;
    line-height: 20px;
  }
}

.checkout__title {
  width: 100%;
  margin-top: 0;
  margin-bottom: 48px;
  padding-right: 10px;
  padding-left: 10px;
  font-size: 35px;
  line-height: 41px;

  &--note {
    margin-bottom: 30px;
  }

  @include mobile {
    margin-bottom: 15px;
  }
}

.checkout__note {
  margin-top: 16px;
  margin-bottom: 0;

  &--border {
    position: relative;
    margin-top: 43px;
    margin-right: 10px;
    margin-left: 30px;
    padding-top: 22px;
    padding-right: 30px;
    padding-bottom: 22px;
    padding-left: 50px;
    font-weight: 500;
    border: 2px solid $red;

    svg {
      position: absolute;
      top: 50%;
      left: -20px;
      color: $white;
      transform: translateY(-50%);
    }
  }

  @include desktop {
    &--border {
      margin-top: 13px;
    }
  }

  @include mobile {
    width: 100%;
    margin-top: 11px;

    &--border {
      max-width: 600px;
      margin-top: 0;
      padding-top: 10px;
      padding-left: 35px;
      padding-right: 8px;
      padding-bottom: 10px;
    }
  }
}

.checkout__title + .checkout__note {
  margin-bottom: 35px;
  padding-left: 10px;
  padding-right: 10px;

  @include mobile {
    margin-top: 0;
    margin-bottom: 10px;
  }
}

.checkout__item {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 30px;
  padding-right: 10px;
  padding-left: 10px;

  &.input {
    position: relative;
    width: 100%;
    margin-bottom: 40px;
  }

  &--halfwidth.input {
    width: 50%;
  }

  &--halfwidth.radio {
    width: 40%;
    margin-bottom: 0;
  }

  &:last-child {
    margin-bottom: 0;
  }

  @include desktop {
    &--halfwidth.radio {
      width: 50%;
      margin-bottom: 30px;
    }

    &:last-child {
      margin-bottom: 30px;
    }
  }

  @include tablet {
    &--halfwidth.input {
      width: 100%;
    }
  }

  @include mobile {
    margin-bottom: 15px;

    &:last-child {
      margin-bottom: 15px;
    }

    &.input {
      margin-bottom: 15px;
    }

    &--halfwidth.radio {
      width: 100%;
      margin-bottom: 15px;
    }
  }
}

.checkout__error {
  display: none;
  position: absolute;
  left: 10px;
  bottom: -20px;
  margin-top: 5px;
  margin-bottom: 0;
  font-size: 12px;
  line-height: 14px;
  color: $red;

  @include tablet {
    position: static;
  }

  @include mobile {
    font-size: 10px;
    line-height: 12px;
  }
}

input.invalid + .checkout__error,
textarea.invalid + .checkout__error {
  display: block;
}

.checkout .checkout__link {
  display: inline;
  color: inherit;
  @include link-hover;
}
