.cart a {
  position: relative;

  display: block;
  padding-top: 4px;
  padding-right: 6px;

  &:hover,
  &:focus {
    outline: none;
  }

  & svg {
    // fill: transparent;
    fill: $tan;
    stroke: $tan;

    transition: all $transition;
  }

  &:hover svg,
  &:focus svg {
    fill: $sandal;
    stroke: $sandal;
  }
}

.cart__counter {
  position: absolute;
  right: 2px;
  top: 2px;

  display: flex;
  justify-content: center;

  align-items: center;
  min-width: 12px;
  min-height: 12px;
  margin: 0;

  border-radius: 100px;
  background-color: $red-ribbon;
}

.cart__counter span {
  padding: 2px;

  color: $white;
  font-size: 8px;
  line-height: 8px;
}
