.main h2 {
  display: inline-block;
  font-size: 75px;
  line-height: 90px;
  font-weight: 700;

  color: $black;
}

.main h3 {
  font-size: 35px;
  line-height: 41px;

  color: $black;
}

// .main__slider {
//   position: absolute;
//   top: 0;
//   right: 0;
//   left: 0;
// }

.main__scroll-down {
  position: absolute;
  top: 745px;
  left: 50%;
  transform: translateX(-50%);

  font-size: 20px;
  line-height: 28px;
  font-weight: 500;
  color: $white;
  text-align: center;

  background-color: transparent;

  z-index: 50;

  & a {
    @include link-reset;
    @include hover;

    display: inline-block;

    & svg {
      display: block;
      margin: 18px auto 0 auto;

      fill: $white;
    }
  }
}

.main__about-us {
  position: relative;

  margin-top: -108px;
  margin-left: $desktop-oversize-gap;
  padding-top: 92px;
  padding-left: 270px;
  padding-bottom: 4px;
  padding-right: calc((100vw - 1600px) / 2 + 135px);

  line-height: 22px;

  background-color: $concrete;

  z-index: 10;
}

.main__catalog {
  margin-top: -1px;
  margin-left: $desktop-oversize-gap;
  margin-bottom: 149px;
}

.main__faq {
  margin-right: calc((100vw - 1600px) / 2 + 270px); ;
}

.main__contacts {
  margin-bottom: 200px;
  margin-left: $desktop-oversize-gap;
}

@include desktop-l {
  .main__about-us {
    margin-left: $desktop-gap;
    padding-right: calc(8.3% + 135px);
  }

  .main__catalog {
    margin-left: $desktop-gap;
  }

  .main__faq {
    margin-right: calc(8.3% + 270px);
  }

  .main__contacts {
    margin-left: $desktop-gap;
  }
}

@include desktop {
  .main h2 {
    font-size: 60px; //    . 80%  
    line-height: 72px;
  }

  .main h3 {
    font-size: 28px;
    line-height: 34px;
  }
}

@include tablet {
  .main h2 {
    font-size: 28px;
    line-height: 34px;
  }

  .main h3 {
    font-size: 18px;
    line-height: 21px
  }

  .main__scroll-down {
    display: none;
  }

  .main__about-us {
    position: static;
    margin-left: 0;
    margin-top: -1px;
    padding-top: 36px;
    padding-right: $tablet-gap;
    padding-left: $tablet-gap;

    font-size: 14px;
    line-height: 20px;
  }

  .main__catalog {
    margin-left: 0;
    margin-bottom: 42px;
    // padding-right: $tablet-gap;
  }

  .main__faq {
    margin-right: 0;
  }

  .main__contacts {
    margin-left: 0;
  }
}

@include mobile {
  .main__about-us {
    padding-right: $mobile-gap;
    padding-left: $mobile-gap;
  }

  .main__catalog {
    margin-left: 0;
  }
}

.main__inner {
  @media (min-width: 1600px) {
    margin-right: calc((100vw - 1600px) / 2);
  }
}
