//   
@mixin title-reset {
  font-weight: normal;
  margin: 0;
  padding: 0;
}

@mixin field-reset {
  padding: 0;
  margin: 0;
  background-color: transparent;
  border: none;
  border-radius: 0;
  outline: none;
  appearance: none;
}

@mixin list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin link-reset {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

@mixin button-reset {
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
  color: inherit;
}

// 
// 1921  
@mixin desktop-oversize {
  @media (min-width: $width-desktop-max + 1px) {
    @content;
  }
}


//  1920
@mixin desktop-l {
  @media (max-width: $width-desktop-max) {
    @content;
  }
}

//  1400
@mixin desktop {
  @media (max-width: $width-desktop-large - 1px) {
    @content;
  }
}
//  1024
@mixin tablet {
  @media (max-width: $width-desktop - 1px) {
    @content;
  }
}
//  768
@mixin mobile {
  @media (max-width: $width-tablet - 1px) {
    @content;
  }
}

// 
@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

// 
@mixin font-face($name, $filename, $weight) {
  @font-face {
    font-family: $name;
    font-weight: $weight;
    font-style: normal;
    font-display: swap;
    src: url("../fonts/"+$filename+".woff2") format("woff2"),
      url("../fonts/"+$filename+".woff") format("woff");
  }
}

// 
@mixin hover {
  will-change: opacity;
  transition: opacity $transition;

  &:hover,
  &:focus {
    outline: none;
    opacity: 0.5;
  }

  &:active {
    outline: none;
    opacity: 1;
  }
}

@mixin link-hover {
  transition: all $transition;

  &:hover,
  &:focus {
    color: $red-ribbon;
    text-decoration: underline;
    outline: none;
  }

  &:active {
    color: $mine-shaft;
    outline: none;
    opacity: 0.6;
  }
}

// 
@mixin shadow {
  box-shadow: 0px 20px 0px -10px $shadow;
}

