.popup-cart {
  z-index: 100;
}

.popup-cart__overlay {
  position: fixed;
  top: 0;
  left: 0;

  width: 100vw;
  height: 100vh;

  background-color: rgba(109, 109, 109, 0.9);
}

.popup-cart__content {
  position: fixed;
  top: 50%;
  left: 50%;

  width: 790px;
  margin-top: -283px;
  margin-left: -395px;
  padding: 98px 135px 100px;

  background-color: #ffffff;
}

.popup-cart__content h2 {
  margin: 0;
  margin-bottom: 48px;

  font-weight: 700;
  font-size: 35px;
  line-height: 41px;
  color: #000000;
}

.popup-cart__cart-card {
  margin-bottom: 50px;
}

.popup-cart__to-catalog,
.popup-cart__to-cart {
  width: 250px;
  min-height: 45px;
  padding: 10px 20px;

  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

.popup-cart__to-catalog {
  margin-right: 16px;
}

.popup-cart__to-cart {
  display: inline-flex;
  align-items: center;
  justify-content: center;

  text-align: center;
  text-decoration: none;
}

.popup-cart__close {
  @include hover;
  @include button-reset;

  position: fixed;
  top: 5.1%;
  right: 55px;

  font-weight: 300;
  font-size: 18px;
  line-height: 140%;
  color: #ffffff;
}

@media (max-width: 1100px) {
  .popup-cart__close span {
    display: none;
  }

  .popup-cart__close {
    @include button-reset;
    @include hover;

    top: calc(50vh - 283px + 32px);
    right: calc((100vw - 790px) / 2 + 32px);

    display: block;
    width: 50px;
    height: 50px;

    background-color: #ffffff;
    background-image: url("../img/icon-close.svg");
    background-repeat: no-repeat;
    background-position: center;
  }
}

@include mobile {
  .popup-cart__content {
    top: 0;
    left: 0;

    width: 100%;
    margin: 0;
    padding: 80px 16px;
  }

  .popup-cart__content h2 {
    max-width: 500px;
    margin: 0 auto;
    margin-bottom: 50px;
    padding: 0 10px;

    font-size: 28px;
    line-height: 34px;

    text-align: center;
  }

  .popup-cart__to-catalog {
    display: block;
    margin: 0 auto;
    margin-bottom: 20px;
  }

  .popup-cart__to-cart {
    display: flex;
    margin: 0 auto;
  }

  .popup-cart__close span {
    display: none;
  }

  .popup-cart__close {
    top: 16px;
    right: 16px;
  }
}
