.catalog {
  padding-bottom: 30px;
}

.catalog__wrapper {
  margin-top: -1px;
  padding-right: $desktop-oversize-gap;
  padding-left: $desktop-oversize-padding;

  background-color: $concrete;

  &--transparent {
    background-color: transparent;
  }
}

.catalog h2 {
  margin-bottom: 68px;
}

.catalog .filter {
  padding-bottom: 100px;
}

.catalog fieldset {
  margin-top: 0;
  margin-right: 0;
  margin-left: 0;

  padding-left: $desktop-oversize-padding;
}
