//   flex-grow: 0;
//   flex-basis: 18.5%;
//   margin-right: 1.5%;
// }

.catalog label {
  // width: 100%;
  width: 250px;
}

// .catalog ul {
//   @include list-reset;
// }

.catalog__list {
  @include list-reset;
  overflow: initial; //  ,  
  display: flex;
  flex-wrap: wrap;

  margin-top: -100px;
  margin-right: -20px;
  // margin-right: -1.5%;
  padding-left: $desktop-oversize-padding;

  font-size: 14px;
  line-height: 16px;

  color: $white;
}

.catalog__list li {
  position: relative;

  width: 250px;
  height: 340px;
  margin-right: 20px;
  margin-bottom: 20px;
  // display: flex;
  // flex-grow: 0;
  // flex-basis: 18.5%;
  // margin-right: 1.5%;

  transition: all $transition;

  &:hover,
  &:focus-within {
    @include shadow;
    transform: translateY(-10px);

    & div {
      opacity: 1;
    }
  }
}

.catalog__list div {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;

  display: flex;
  flex-direction: column;

  padding: 13px 25px 25px 25px;

  opacity: 0;

  background-color: rgba(0, 0, 0, 0.7);

  transition: all $transition;
}

.catalog h4 {
  margin-top: 5px;
  margin-bottom: 8px;

  font-size: 20px;
  line-height: 28px;
  font-weight: 700;

  text-transform: uppercase;
}

.catalog table {
  margin-right: 40px;
}

.catalog td {
  padding-top: 8px;
}

.catalog p {
  display: flex;

  margin-top: auto;
}

.catalog .btn {
  margin-right: auto;
  padding: 16px 24px 12px 24px;

  text-transform: uppercase;
}

.catalog a {
  @include link-reset;

  display: inline-block;
  width: 53px;
  height: 45px;

  &:hover {
    opacity: 0.3;
  }
}

.catalog a svg {
  stroke: $white;
}

.catalog pictute {
  line-height: 0;
  font-size: 0;
}

.catalog img {
  line-height: 10px;
  font-size: 10px;
}

.catalog__pagination {
  display: none;
}

@include desktop-l {
  .catalog__wrapper {
    padding-right: $desktop-gap;
  }
}

@include tablet {
  .catalog {
    padding-bottom: 20px;

    font-size: 14px;
    line-height: 16px;
  }

  .catalog__wrapper {
    padding-right: $tablet-gap;
    padding-left: $tablet-gap;
  }

  // .catalog .filter ul {
  //   margin-right: -5%;
  // }

  // .catalog .filter li {
  //   flex-basis: 28%;
  //   margin-right: 5%;
  // }

  // .catalog .filter label {
  //   width: 100%;
  //   // width: 250px;
  // }
  .catalog h2 {
    margin-top: 25px;
    margin-bottom: 48px;
  }

  .catalog .filter {
    padding-bottom: 64px;
  }

  .catalog fieldset {
    padding-left: 0;
  }

  .catalog legend {
    margin-bottom: 20px;
  }

  .catalog label {
    width: 136px;
  }

  .catalog__list {
    margin-top: -70px;
    margin-left: 0;
    margin-right: -16px;
    padding-left: 0;
  }

  .catalog__list > li {
    width: 136px;
    height: 185px;
    margin-right: 16px;
    margin-bottom: 16px;

    &.is-open {
      animation: fadeIn 0.3s;
      @keyframes fadeIn {
        from {opacity :0;}
        to {opacity: 1;}
      }
    }
  }

  .catalog h4 {
    margin-bottom: 6px;

    font-size: 16px;
    line-height: 18px;
  }

  .catalog__list div {
    padding: 8px;
  }

  .catalog table {
    margin-right: 0;

    font-size: 11px;
    line-height: 14px;
  }

  .catalog td {
    padding-top: 4px;
  }

  .catalog__list .btn {
    padding: 6px 10px 4px 10px;

    font-size: 10px;
    line-height: 12px;
  }

  .catalog a {
    width: 27px;
    height: 23px;

    & svg {
      width: 27px;
      height: 23px;
    }
  }

  .catalog__pagination {
    @include list-reset;

    display: flex;
    justify-content: center;

    margin-top: 18px;
    margin-right: -16px;
  }

  .catalog__pagination .btn {
    display: none;
    display: flex;
    justify-content: center;
    align-items: center;

    width: 60px;
    height: 60px;
    margin-right: 16px;

    color: $dove-gray;

    border: solid 2px $dove-gray;


    &:focus,
    &:hover{
      box-shadow: none;
      transform: none;
      opacity: 0.6;
    }

    &:active {
      box-shadow: none;
      transform: none;
    }

    &[disabled],
    &:disabled {
      background-color: transparent;
      opacity: 0.6;
    }
  }

}

@include mobile {
  .catalog__wrapper {
    padding-right: $mobile-gap;
    padding-left: $mobile-gap;
  }
