*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  background-color: $silver;
  min-width: $width-mobile;
  width: 100%;
  height: 100%;
  color: $mine-shaft;
  font-family: $font;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  line-height: 25px;
}

body.popup {
  max-height: 100vh;
  overflow: hidden;
}

main {
  flex: 1;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  margin: -1px;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  box-sizing: border-box;
  margin: 0 auto;
  width: 100%;
  padding: 0 160px;

  @include mobile {
    padding: 0 16px;
  }
}

.hidden {
  display: none;
}
