.confirmation {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  max-width: 925px;
  width: 55%;
  margin-right: auto;
  margin-bottom: 200px;
  margin-left: 15.4%;
  margin-top: -106px;
  padding-top: 98px;
  padding-right: 125px;
  padding-bottom: 98px;
  padding-left: 135px;
  background-color: $white;

  &.hidden {
    display: none;
  }

  @include desktop {
    width: auto;
    max-width: 790px;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 20px;
  }

  @include tablet {
    width: 77%;
    padding-top: 50px;
    padding-right: 50px;
    padding-bottom: 50px;
    padding-left: 50px;
  }

  @include mobile {
    padding-top: 15px;
    padding-right: 15px;
    padding-bottom: 10px;
    padding-left: 15px;

  }
}

.confirmation .confirmation__title {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 35px;
  line-height: 41px;
  color: $black;

  @include desktop {
    font-size: 28px;
    line-height: 34px;
  }

  @include tablet {
    width: 100%;
    font-size: 18px;
    line-height: 21px;
    text-align: center;
  }

  @include mobile {
    text-align: left;
  }
}

.confirmation__icon {
  margin-right: 30px;
  color: $conifer;

  @include tablet {
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 25px;
  }

  @include mobile {
    margin-bottom: 15px;
    width: 40px;
    height: 40px;
  }
}

.confirmation__text {
  width: 100%;
  margin-top: 50px;
  margin-bottom: 0;
  line-height: 22px;

  @include tablet {
    margin-top: 20px;
  }

  @include mobile {
    margin-top: 10px;
    padding-right: 10px;
    font-size: 14px;
    line-height: 20px;
  }
}
