.contacts {
  // display: flex;
  // flex-wrap: wrap;
  padding-top: 18px;
  padding-bottom: 60px;
  padding-right: $desktop-oversize-gap;
  padding-left: $desktop-oversize-padding;

  background-color: $concrete;
}

// .contacts h2 {
  //   flex-basis: 100%;
  // }

.contacts h2 {
  margin-bottom: 58px;
}

.contacts__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  padding-left: $desktop-oversize-padding;
}

.contacts p {
  // margin-right: 10px;
  // width: 240px;
  width: 252px;
}

.contacts span {
  color: $dove-gray;
}

.contacts__legal {
  // flex-basis: 38%;
  flex-basis: 40%;
}

.contacts a {
  @include link-reset;
  @include hover;

  display: block;
}

@include desktop-l {
  .contacts {
    padding-right: $desktop-gap;
  }
}

@include desktop {
  .contacts p {
    width: auto;
    flex-basis: 28%;
  }
}

@include tablet {
  .contacts {
    margin-bottom: 50px;
    padding-right: $tablet-gap;
    padding-bottom: 6px;
    padding-left: $tablet-gap;

    font-size: 14px;
    line-height: 20px;
  }

  .contacts h2 {
    margin-bottom: 37px;
  }

  .contacts__wrapper {
    flex-direction: column;

    padding-left: 0;
  }

  .contacts p {
    width: auto;
    margin-top: 5px;
    margin-right: 0;
    margin-bottom: 5px;
    padding-right: 35px;
  }

  .contacts a {
    margin-bottom: 8px;
  }
}

@include mobile {
  .contacts {
    padding-right: $mobile-gap;
    padding-left: $mobile-gap;

  }
}


