.cart-card {
  display: flex;

  &--advanced {
    position: relative;

    width: 790px;
    background-color: #ffffff;
  }
}

.cart-card__image-wrap {
  width: 115px;
  height: 156px;

  background-color: #c4c4c4;
}

.cart-card__image-wrap picture {
  font-size: 0;
  line-height: 0;
}

.cart-card__data-wrap {
  //display: flex;
  flex-direction: column;
  width: 400px;
  align-content: flex-start;
  padding: 0 36px 20px 17px;

  color: #000000;

  &--advanced {
    width: auto;
    //display: flex;
  }
}

.cart-card__name {
  display: flex;
  align-items: flex-end;
  margin: 0;
  //max-width: 230px;
  min-height: 75px;

  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
  text-align: left;

  &--advanced {
    align-items: center;
    min-height: 94px;
    max-width: none;
    width: 180px;
    margin-right: 88px;

    font-size: 18px;
    line-height: 21px;
  }
}

.cart-card__price--advanced {
  position: relative;
}

.cart-card__quantity-control {
  position: absolute;
  right: 119px;
  top: 39px;

  display: flex;
  align-items: center;

  font-size: 16px;
  color: #6d6d6d;
}

.cart-card__quantity-decrease,
.cart-card__quantity-increase {
  @include button-reset;

  width: 30px;
  height: 30px;

  background-repeat: no-repeat;
  background-position: center;

  border: 2px solid #6d6d6d;
  opacity: 0.7;

  &:hover,
  &:focus {
    opacity: 1;
  }

  &:active {
    opacity: 0.7;
  }
}

.cart-card__quantity-decrease {
  background-image: url("../img/icon-minus.svg");
}

.cart-card__quantity-increase {
  background-image: url("../img/icon-plus.svg");
}

.cart-card__quantity-value {
  display: block;
  width: 55px;
  padding: 0 5px;

  text-align: center;
}

.cart-card__delete {
  @include button-reset;

  position: absolute;
  right: -64px;
  top: 42px;

  width: 17px;
  height: 23px;

  background-image: url("../img/icon-trash.svg");

  opacity: 0.7;

  &:hover,
  &:focus {
    opacity: 1;
  }

  &:active {
    opacity: 0.7;
  }
}

.cart-card__price {
  //display: block;
  //width: 343px;
  margin: 0;
  //margin-top: -30px;
  padding-bottom: 2px;
  //padding-left: 240px;
  min-width: 80px;

  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  text-align: right;
  vertical-align: bottom;

  &--advanced {
    width: 300px;
    vertical-align: middle;
    padding-top: 14px;
    // padding-left: 470px;
    // margin-top: -50px;
  }
}

.cart-card__size {
  display: block;
  margin: 0;
  padding-top: 4px;

  font-size: 16px;
  line-height: 19px;

  &--advanced {
    padding-top: 0;
  }
}

@include tablet {
  .cart-card--advanced {
    width: auto;
  }

  .cart-card--advanced tr:first-of-type {
    display: flex;
    flex-direction: column;
  }

  .cart-card__data-wrap--advanced {
    padding-bottom: 0;
    padding-left: 30px;
  }

  .cart-card__name--advanced {
    min-height: 105px;
    align-items: flex-start;
    padding-top: 10px;
    padding-bottom: 40px;
  }

  .cart-card__price--advanced {
    position: static;
    text-align: left;
  }

  .cart-card__size--advanced {
    margin-top: -80px;
  }

  .cart-card__quantity-control {
    right: 40px;
    top: 15px;
  }

  .cart-card__delete {
    right: 40px;
    top: auto;
    bottom: 15px;
  }
}

@include mobile {
  .cart-card:not(.cart-card--advanced) {
    position: relative;
    justify-content: center;
  }

  .cart-card--advanced {
    padding: 16px;
  }

  .cart-card__image-wrap {
    height: 81px;
    width: 60px;
    min-width: 60px;
  }

  .cart-card__image-wrap img {
    height: 81px;
    width: 60px;
  }

  .cart-card__data-wrap {
    width: auto;
    padding-bottom: 0;
    padding-right: 0;
    min-height: 81px;
  }

  .cart-card:not(.cart-card--advanced) tbody {
    display: flex;
    flex-direction: column;
  }

  .cart-card__name:not(.cart-card__name--advanced) {
    width: 100%;
    min-height: auto;
    margin-bottom: 5px;

    font-size: 14px;
    line-height: 16px;
  }

  .cart-card__name--advanced {
    width: auto;
    margin: 0;
    min-height: auto;
    padding: 0;
    max-width: 120px;

    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
  }

  .cart-card__data-wrap--advanced {
    padding: 0;
    padding-left: 13px;
    margin-top: -6px
  }

  .cart-card__price {
    position: absolute;
    bottom: 0;

    width: auto;
    padding-left: 0;
    margin: 0;

    font-size: 14px;
    line-height: 16px;
    text-align: left;
  }

  .cart-card__price--advanced {
    position: absolute;
    bottom: 0;
    padding-bottom: 0;
  }

  .cart-card__size {
    width: auto;
    margin-bottom: 20px;

    font-size: 12px;
    line-height: 14px;
  }

  .cart-card__size--advanced {
    margin: 0;
  }

  .cart-card__quantity-control {
    right: -125px;
    bottom: 10px;
  }

  .cart-card__quantity-decrease,
  .cart-card__quantity-increase {
    width: 24px;
    height: 24px;
  }

  .cart-card__quantity-value {
    font-size: 14px;
    line-height: 17px;
    width: 27px;
  }

  .cart-card__delete {
    right: -125px;
    bottom: 60px;
  }
}

