.header {
  position: absolute;
  top: 50px;
  right: 0;
  left: 0;


  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 24px $desktop-oversize-gap 26px 35px;
  margin-left: calc((100vw - 1600px) / 2 + 135px);
  // margin-top: 51px;

  background-color: white;

  z-index: 10;
}

.header a {
  @include link-reset;
}

.header__logo {
  margin-right: 35px;


  &--is-disabled a {
    cursor: none;
    pointer-events: none;
  }
}

.header__logo a {
  display: block;
  line-height: 10px;
}


.header__toggler {
  @include button-reset;
  @include hover;

  display: none;
  width: 24px;
  height: 20px;

  background-image: url("../img/icon-burger.svg");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.header__cart {
  margin-right: 23px;
  margin-top: 4px;
}

.header__contacts {
  @include list-reset;

  display: flex;
  flex-wrap: wrap;

  font-weight: 700;
}

.header__contacts li {
  margin-right: 20px;
}

.header__nav {
  margin-right: auto;
}

//    ,   ,   


@media (max-width: 1440px)   {
  .header__contacts li:first-of-type {
    display: none;
  }
}

@media (max-width: 1263px) {
  .header__contacts {
    display: none;
  }
}

@media (max-width: 1090px) {
  .header__social {
    display: none;
  }
}

@include desktop-l {
  .header {
    margin-left: 15.4%;
    padding-right: $desktop-gap;
  }
}


@include tablet {
  .header { //    100%  -  ??
    top: 0;

    margin-top: $tablet-gap;
    margin-right: $tablet-gap;
    margin-left: $tablet-gap;
    padding: 12px 0 0 0;

    font-size: 14px;
    line-height: 17px;
  }

  //    
  .header--menu-open {
    .header__contacts li:first-of-type {
      display: unset; // 
    }

    .header__contacts,
    .header__nav,
    .header__social {
      display: flex;

      animation: fadeIn 0.3s;

      @keyframes fadeIn {
        from {
          opacity: 0;
        }

        to {
          opacity: 1;
        }
      }
    }

    .header__toggler {
      background-image: url("../img/icon-close.svg");
    }
  }

  //    
  .header__contacts,
  .header__nav,
  .header__social {
    display: none;
  }

  .header__logo {
    margin-left: 12px;
    margin-right: auto;
    margin-bottom: 12px;
  }

  .header__logo svg {
    width: 88px;
    height: 30px;
  }

  .header__toggler {
    display: block;
    margin: -2px 17px 12px 60px;
  }

  .header__toggler svg {
    width: 24px;
    height: 20px;
  }

  .header__cart {
    position: absolute;
    top: 15px;
    right: 60px;
    margin-right: 0;
    margin-top: -2px;
  }

  .header__nav {
    margin: 2px 17px 0 12px;
  }

  .header__nav,
  .header__social {
    flex-basis: 100%;
  }

  .header__contacts {
    flex-direction: column;

    margin-right: 17px;
    margin-left: 12px;

    font-size: 16px;
    line-height: 19px;
  }

  .header__contacts li {
    margin-bottom: 0;
    padding-top: 16px;
    padding-bottom: 17px;
  }
}

@include mobile {
  .header {
    margin-top: $mobile-gap;
    margin-right: $mobile-gap;
    margin-left: $mobile-gap;
  }
}
