.slider-main {
  background-color: $concrete;
}
.slider-main__slide {
  display: flex;
  flex-direction: column;
  // align-items: center;
  justify-content: center;

  height: 978px;
  overflow: hidden;
}
.slider-main__card {
  color: $white;

  background: linear-gradient(to right, $meteorite, $disco);
}

.slider-main__card {
  position: absolute;
  left: 0;
  top: 280px;

  box-sizing: content-box;

  width: 495px;
  padding-left: $desktop-oversize-gap;
  padding-right: 160px;
  padding-bottom: 40px;
}

.slider-main__card h3 {
  font-size: 100px;
  line-height: 120px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  // font-family: ;
  color: inherit;

  margin-top: 40px; //    
  margin-bottom: 20px; //    
  font-size: 75px; //    
  line-height: 100px; //    
}

.slider-main__card p {
  margin-bottom: 34px;

  font-size: 20px;
  line-height: 28px;
}

.slider-main__card span {
  font-size: 60px;
  font-weight: 700;
  line-height: 72px;

  &:last-of-type {
    margin-left: 3px;
  }
}

.slider-main__card .btn {
  position: absolute;
  bottom: -32px;
  // left: ;

  min-width: 210px;
  max-width: 350px;
  padding-top: 24px;
  padding-bottom: 18px;

  font-size: 24px;
  line-height: 24px;
}

.slider-main picture {
  min-width: 100%;
  min-height: 100%;

  font-size: 0;
  line-height: 0;
}

.slider-main img {
  min-width: 100%;
  min-height: 100%;
  height: auto;
  object-fit: cover;

//   font-size: 0;
//   line-height: 0;
}

@include desktop-l {
  .slider-main__card {
    padding-left: $desktop-gap;
    padding-right: $desktop-gap;
  }
}

@include desktop {
  .slider-main__card h3 {
    font-size: 80px;
    line-height: 96px;
  }
}

@include tablet {
  .slider-main__slider {
    padding-bottom: 40px;
  }

  .slider-main__slide {
    height: auto;
    overflow: visible;
  }

  .slider-main picture {
    min-width: auto;
    min-height: auto;
  }

  .slider-main img {
    min-height: 270px;
  }

  .slider-main__card {
    position: relative;
    top: 0;
    left: 0;

    width: auto;
    padding-left: $tablet-gap;
    padding-bottom: 42px;
    padding-right: $tablet-gap;
  }
  .slider-main__card h3 {
    margin-top: 12px;
    margin-bottom: 10px;

    font-size: 28px;
    line-height: 33px;
  }

  .slider-main__card p {
    margin-top: 8px;
    margin-bottom: 0;

    font-size: 14px;
    line-height: 20px;
  }

  .slider-main__card span {
    font-size: 20px;
    line-height: 24px;
  }

  .slider-main .btn {
    left: 50%;
    bottom: -28px;
    transform: translateX(-50%);

    min-width: 96px;
    max-width: 200px;

    font-size: 16px;
    line-height: 19px;

    &:hover,
    &:focus {
      transform: translate(-50%, -10px);
    }

    &:active {
      transform: translate(-50%, 0);
    }
  }
}

@include mobile {
  .slider-main__card {
    padding-left: $mobile-gap;
    padding-right: $mobile-gap;
  }
}
