.social {
  @include list-reset;

  display: flex;
}

.social li {
  margin-right: 18px;
  &:last-child {
    margin-right: 0;
  }
}

.social a {
  @include hover;

  display: block;
  line-height: 10px;
}

.social svg {
  fill: $mine-shaft;
  stroke: $mine-shaft;
}

@include tablet {
  .social {
    justify-content: center;
    padding-top: 11px;
    padding-bottom: 11px;

    background-color: $mine-shaft;
  }

  .social svg {
    fill: $white;
    stroke: $white;
  }

  .social li:last-child {
    margin-right: 0;
  }
}
