.input {
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;

  label {
    margin-bottom: 20px;
  }

  input {
    width: 100%;
    height: 60px;
    padding-top: 19px;
    padding-right: 20px;
    padding-bottom: 19px;
    padding-left: 20px;

    border: 2px solid $dove-gray;

    &.invalid {
      border-color: $red;
    }
  }

  textarea {
    width: 100%;
    height: 120px;
    padding-top: 19px;
    padding-right: 20px;
    padding-bottom: 19px;
    padding-left: 20px;

    border: 2px solid $dove-gray;
    resize: none;

    &.invalid {
      border-color: $red;
    }
  }

  @include mobile {
    font-size: 14px;
    line-height: 17px;

    label {
      margin-bottom: 15px;
    }
  }
}
