.nav__list {
  @include list-reset;

  display: flex;
}

.nav__list li {
  margin-right: 20px;
}

.nav__list a {
  text-transform: uppercase;
  letter-spacing: 0.1em;

  &:hover,
  &:focus {
    color: $red-ribbon;

    transition: all $transition;
  }

  &:active {
    color: $mine-shaft;
    opacity: 0.3;
  }
}


@include tablet {
  .nav__list {
    flex-direction: column;
  }

  .nav__list li {
    margin-bottom: 0;
    padding-top: 19px;
    padding-bottom: 18px;
  }
}
