.privacy-policy__title-wrap {
  position: relative;

  padding: 76px 69px 183px $desktop-oversize-gap;
  margin-top: 230px;
  margin-right: 20px;
  margin-right: calc((100vw - 1920px) / 2 + 295px);

  background-color: #f3f3f3;
}

.privacy-policy__title-wrap h1 {
  display: inline-block;
  margin: 0;
  padding-left: 0;

  font-size: 75px;
  line-height: 88px;
  font-weight: 700;
  color: #000;
  background: -webkit-linear-gradient(0deg,#351e66,#981262);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.privacy-policy__text {
  position: relative;

  max-width: 1330px;
  margin-left: $desktop-oversize-gap;
  margin-top: -104px;
  margin-bottom: 200px;
  padding: 100px 135px 87px;

  background-color: #ffffff;
  box-sizing: border-box;
}

.privacy-policy__text p {
  margin: 0;
  margin-bottom: 10px;

  font-size: 16px;
  line-height: 22px;
  color: #373737;
}

.privacy-policy__text h2 {
  margin: 0;
  margin-bottom: 3px;
  padding-top: 7px;

  font-size: 35px;
  line-height: 49px;
  color: #373737;
}

@include desktop-l {
  .privacy-policy__text {
    margin-left: $desktop-gap;
    margin-right: $desktop-gap;
  }

  .privacy-policy__title-wrap h1 {
    padding-left: $desktop-gap;
  }
}

@include desktop {
  .privacy-policy__text {
    margin-left: 5%;
    margin-right: 5%;
    padding-left: 100px;
    padding-right: 100px;
  }

  .privacy-policy__title-wrap {
    width: 100%;
  }
}

@include tablet {
  .privacy-policy__title-wrap {
    margin-top: 100px;
  }

  .privacy-policy__title-wrap h1 {
    font-size: 55px;
    line-height: 65px;
  }

  .privacy-policy__text {
    margin-left: $tablet-gap;
    margin-right: $tablet-gap;
    margin-bottom: 100px;
    padding: 70px;
  }

  .privacy-policy__text h2 {
    font-size: 28px;
    line-height: 36px;
  }
}

@include mobile {
  .privacy-policy__title-wrap {
    margin-top: 0;
    padding-top: 122px;
    padding-bottom: 106px;

  }

  .privacy-policy__title-wrap h1 {
    font-size: 28px;
    line-height: 33px;
    padding-left: 16px;
  }

  .privacy-policy__text {
    margin-top: -54px;
    margin-left: 16px;
    margin-right: 16px;
    margin-bottom: 50px;
    padding: 11px 16px;
    padding-bottom: 1px;
  }

  .privacy-policy__text p {
    font-size: 14px;
    line-height: 20px;
  }

  .privacy-policy__text h2 {
    margin-bottom: 9px;
    padding-top: 1px;

    font-size: 18px;
    line-height: 25px;
  }
}
