.cart {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}

.cart h1 {
  display: inline-block;
  margin: 0;
  padding-left: 0;

  font-size: 75px;
  line-height: 88px;
  font-weight: 700;
  color: #000;
  background: -webkit-linear-gradient(0deg,#351e66,#981262);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.cart__title-wrap {
  position: relative;

  display: inline-block;
  width: calc(1060px + calc((100vw - 1600px)/2));
  padding: 76px 69px 183px $desktop-oversize-gap;
  margin-top: 230px;
  margin-right: 20px;
  margin-bottom: 100px;

  background-color: #f3f3f3;
}

.cart__empty-cart-message {
  position: absolute;
  bottom: 87px;
  left: calc(((100vw - 1600px) / 2) + 135px);

  margin: 0;

  font-size: 16px;
  line-height: 19px;

  color: #373737;
}

.cart__empty-cart-message a {
  @include link-reset;

  display: inline-block;
  padding: 0;
  height: 17px;

  border-bottom: 1px solid #373737;
}

.cart--checkout .cart__title-wrap {
  flex-grow: 1;
  align-self: stretch;
  width: auto;
  margin-bottom: 0;
}

.cart__order-data {
  position: relative;

  display: inline-block;
  width: 385px;
  min-height: 347px;
  margin-top: 230px;
  padding: 50px;

  vertical-align: top;
  color: #000000;

  background-color: #ffffff;
}

.cart--checkout .cart__order-data {
  @include desktop {
    margin-right: auto;
  }
}

.cart__order-data h3 {
  margin: 0;
  margin-bottom: 30px;

  font-size: 35px;
  line-height: 41px;
}

.cart__order-data p {
  margin: 0;

  line-height: 19px;
}

.cart__order-data table {
  width: 100%;
  border-collapse: collapse;
}

.cart__order-data tr:not(:last-of-type) td {
  padding: 0 0 24px;
}

.cart__order-data tr:first-of-type td {
  padding: 23px 0 24px;
}

.cart__order-data tr:last-of-type td {
  padding: 31px 0 10px;
  border-top: 1px solid #c4c4c4;
}

.cart__total-quantity,
.cart__total-sum {
  text-align: right;
}

.cart__order-data tr:last-of-type {
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
}

.cart__order-data a {
  position: absolute;
  bottom: -27px;
  left: 50%;

  display: flex;
  align-items: center;
  justify-content: center;
  width: 285px;
  min-height: 60px;
  padding: 10px;
  margin-left: -142px;
}

.cart__list {
  @include list-reset;
  position: relative;
  max-width: 800px;

  margin-left: calc((100vw - 1600px) / 2 + 135px);
  margin-top: -206px;
}

.cart__list li {
  margin-bottom: 20px;
}

@include desktop-l {
  .cart__title-wrap {
    padding-left: $desktop-gap;
  }

  .cart__empty-cart-message {
    left: 24%;
  }
}

@include desktop {
  .cart__title-wrap {
    width: 100%;
  }

  .cart__order-data {
    width: 790px;
    order: 1;
    margin: 0 auto;
    margin-bottom: 100px;
    padding: 50px 200px;
  }

  .cart__list {
    margin-left: auto;
    margin-right: auto;
  }
}

@include tablet {
  .cart__title-wrap {
    margin-top: 120px;
  }
  .cart__list {
    max-width: none;
    width: 77%;
  }

  .cart__order-data {
    width: 77%;
    padding: 50px 150px;
  }
}

@include mobile {
  .cart h1 {
    font-size: 28px;
    line-height: 34px;
  }

  .cart__title-wrap {
    height: 281px;
    margin-top: 0;
    margin-right: 0;
    width: 100%;
    padding: 118px 20px 80px 16px;
  }

  .cart__empty-cart-message {
    left: 16px;
    bottom: 48px;

    max-width: 288px;

    font-size: 14px;
    line-height: 16px;
  }

  .cart__empty-cart-message a {
    height: 13px;
  }

  .cart__list {
    width: 288px;
    margin-left: auto;
    margin-right: auto;
    margin-top: -180px;
  }

  .cart__list li {
    margin-bottom: 17px;
  }

  .cart__order-data {
    width: 100%;
    padding: 16px;
    min-height: 287px;
    margin-bottom: 89px;
  }

  .cart__order-data h3 {
    margin-bottom: 13px;

    font-weight: 700;
    font-size: 18px;
    line-height: 21px;
  }

  .cart__order-data p {
    font-size: 14px;
    line-height: 20px;
  }

  .cart__order-data td {
    font-size: 14px;
    line-height: 22px;
  }

  .cart__order-data tr:first-of-type td {
    padding: 29px 0 30px;

    border-bottom: 1px solid #c4c4c4;
  }

  .cart__order-data tr:last-of-type td {
    padding: 30px 0 8px;
  }
}
