.footer {
  display: flex;
  flex-wrap: wrap;
  padding: 41px 69px 37px $desktop-oversize-gap;
  margin-right: calc((100vw - 1600px) / 2 + 270px);

  font-size: 16px;
  line-height: 140%;
  color: #373737;

  background-color: #ffffff;
}

.footer__copyright {
  display: inline-block;
  margin: 0;
  margin-right: auto;
}

.footer__nav-list {
  @include list-reset;

  display: flex;
  flex-wrap: wrap;
  max-width: 650px;
}

.footer__nav-item:not(:last-child) {
  margin-right: 20px;
}

.footer a {
  @include link-reset;
  @include hover;

  display: inline-block;
  height: 19px;

  border-bottom: 1px solid #373737;
}

@include desktop-l {
  .footer {
    margin-right: 22.38%;
    padding-left: $desktop-gap;
  }
}

@include desktop {
  .footer {
    flex-direction: column;
  }

  .footer__copyright {
    order: 1;
  }

  .footer__nav {
    margin-bottom: 10px;
  }

  .footer__nav-list {
    max-width: none;
    width: 100%;
  }

  .footer__nav-item {
    width: 45%;
    margin-bottom: 20px;

    &:not(:last-child) {
      margin-right: 0;
    }

    &:nth-child(2n + 1) {
      margin-right: 10%;
    }
  }
}

@include tablet {
  .footer {
    margin-left: $tablet-gap;
    margin-right: $tablet-gap;
    margin-bottom: 16px;
    padding-left: 60px;
    padding-right: 30px;
  }
}

@include mobile {
  .footer {
    margin-left: $mobile-gap;
    margin-right: $mobile-gap;
    padding: 33px 24px 31px;

    font-size: 14px;
    line-height: 120%;
  }

  .footer__nav {
    margin-bottom: 17px;
  }

  .footer__nav-item {
    width: 100%;
    margin-bottom: 21px;

    &:nth-child(2n + 1) {
      margin-right: 0;
    }
  }

  .footer__copyright {
    margin-left: -2px;
  }

  .footer a {
    height: 14px;
  }
}
