.filter fieldset {
  padding: 0;
  margin-left: -1px;
  margin-bottom: 40px;

  border: none;
}

.filter legend {
  margin-bottom: 17px;

  font-weight: 500;
}

.filter__list {
  @include list-reset;

  display: flex;
  flex-wrap: wrap;
  margin-right: -20px;
}

.filter__item {
  margin-right: 20px;
  margin-bottom: 20px;
}

.filter label {
  display: block;
  width: 115px;
  min-height: 60px;
  padding-top: 15px;

  color: $dove-gray;
  text-align: center;

  border: 2px solid $dove-gray;

  &:hover {
    background-color: $dove-gray;
    color: $white;
    cursor: pointer;
  }

  &:active {
    background-color: $scorpion;
  }
}

.filter input:focus + label {
  background-color: $dove-gray;
  color: $white;
  cursor: pointer;
}

.filter input:checked + label {
  background-color: $scorpion;
  color: $white;
}

.filter input[disabled] + label,
.filter input:disabled + label {
  opacity: 0.3;
  pointer-events: none;
}

@include tablet {
  .filter fieldset {
    margin-bottom: 26px;
  }

  .filter__list {
    margin-right: -16px;
  }

  .filter__item {
    width: 136px;
    margin-right: 16px;
    margin-bottom: 16px;
  }

  .filter label {
    padding-top: 21px;
    padding-bottom: 19px;
  }
}
