.radio {
  label {
    display: flex;
    align-items: center;
  }

  input {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    border: 0;
    margin: -1px;
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}

.radio__box {
  flex-shrink: 0;
  margin-right: 20px;
  transition: color $transition;

  &--off {
    display: block;
    color: $white;
  }

  &--on {
    display: none;
    color: $jacksons-purple;
  }

  @include mobile {
    width: 24px;
    height: 24px;
  }
}

.radio input:hover ~ .radio__box--off {
  color: rgba($dove-gray, 0.1);
}

.radio input:checked ~ .radio__box--off {
  display: none;
}

.radio input:checked ~ .radio__box--on {
  display: block;
}
